// Depencencies
const utils = require('base/utils.js');

let vue; // eslint-disable-line no-unused-vars
let config;

function init(vueServices) {
  vue = vueServices.getVueInstance();
}

function load($config) {
  config = $config;
}

function get($namespace, $default) {
  if (typeof $namespace === 'string') {
    return utils.getObjectValue(config, $namespace, $default);
  }
  return config;
}

module.exports = {
  init,
  load,
  get,
};



